import * as BABYLON from 'babylonjs';

// This file sets up the Babylon.js environment for additional 3D rendering capabilities.

const canvas = document.getElementById('renderCanvas'); // Get the canvas element
const engine = new BABYLON.Engine(canvas, true); // Create the Babylon.js engine

const createScene = function () {
    const scene = new BABYLON.Scene(engine);
    
    // Create a camera
    const camera = new BABYLON.ArcRotateCamera("camera", Math.PI / 2, Math.PI / 4, 10, BABYLON.Vector3.Zero(), scene);
    camera.attachControl(canvas, true);
    
    // Create a light
    const light = new BABYLON.HemisphericLight("light", new BABYLON.Vector3(1, 1, 0), scene);
    
    // Create a simple box
    const box = BABYLON.MeshBuilder.CreateBox("box", { size: 2 }, scene);
    
    // Set the box color
    box.material = new BABYLON.StandardMaterial("boxMaterial", scene);
    box.material.diffuseColor = new BABYLON.Color3(1, 0, 0); // Red color
    
    return scene;
};

const scene = createScene();

engine.runRenderLoop(function () {
    scene.render();
});

// Resize the engine on window resize
window.addEventListener('resize', function () {
    engine.resize();
});